  position: absolute;
  top: 0;
  left: -90px;
  pointer-events: none;
}

.m__testimonialSlider p {
  display: block;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 0;
}
.m__testimonialSlider__inner {
